import React, { useState, useEffect } from 'react'
import { Link, useHistory, useRouteMatch } from 'react-router-dom'

import { msk_get } from 'api'

import Styled from 'styled-components'
import search_Icon from 'assets/icons/search_white_24dp.svg'


const ViewPort = Styled.section`
    display: flex;
    align-items: center;
	margin: 0px 0px 60px 0px;

    @media(max-width: 1000px) {
        flex-direction: column;
    }
`
const SearchBar = Styled.section`
	display: flex;
	position: relative;
	align-items: center;
	width: 500px;
	height: 40px;

    @media(max-width: 1000px) {
        width: 90%;
        margin-bottom: 25px;
    }

`
const InputSearchBar = Styled.input`
    background-color: transparent;
    padding: 0px 11px 0px 11px;
    outline: none;
    color: white;
	border: none;
	width: 87%;
	height: 95%;
	border-radius: 6px 0px 0px 0px;
	border-bottom: 2px solid white;
	font-size: 1.2em;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

	::-webkit-input-placeholder {
	   color: #A9A9A9;
	}

	:-moz-placeholder { /* Firefox 18- */
	   color: #A9A9A9;  
	}

	::-moz-placeholder {  /* Firefox 19+ */
	   color: #A9A9A9;  
	}

	:-ms-input-placeholder {  
	   color: #A9A9A9;  
	}
`
const BtnSearch = Styled.button`
    background-color: white;
    width: 13%;
	border: none;
    height: 100%;
    border-radius: 0px 6px 6px 0px;
    transition: 500ms;

    :hover {
    	cursor: pointer;
    	background-color: #C9C9C9;
    }
`
const SearchIcon = Styled.img`
	filter invert(100%);
`
const OptionsCompleteSearch = Styled.section`
	position: absolute;
	z-index: 5;
	color: white;
	overflow: hidden;
	background-color: #000005;
	border: 1px solid #191D1F;
	border-radius: 10px;
	box-shadow: 1px 1px 30px black;
	top: 50px;
	width: 465px;
	height: auto;

    @media(max-width: 1000px) {
        width: 100%;
    }
`

const Option = Styled(Link)`
    display: inline-block;
    text-decoration: none;
    color: white;
	width: 98%;
	cursor: pointer;
	padding: 4px 10px 4px 10px;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	overflow: hidden;
	white-space: nowrap;
    text-overflow: ellipsis;

	:hover {
		background-color: #0D0A0A;
	}
`

const Suggestions = Styled.section`
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	width: 60%;
	justify-content: space-around;
	margin-left: 20px;

    @media(max-width: 1000px) {
        width: 95%;
        margin-left: 0px;
    }
`
const SuggestionBox = Styled(Link)`
    text-decoration: none;
	background-color: #1E1E1E;
	border-radius: 10px;
	margin: 2px 4px;
	transition: 500ms;

	:hover {
		cursor: pointer;
		background-color: #111111;
	}
`
const Suggestion = Styled.p`
	color: white;
	padding: 3px 10px;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
`


const Search = () => {

	const [inputSearch, setInputSearch] = useState('')
	const [suggestions, setSuggestions] = useState([])
	const [autoComplete, setAutoComplete] = useState(null)

	let { url } = useRouteMatch();
	let history = useHistory()


    const filterSearch = async (value) => {

	    if (value.length > 1) {
	    	setAutoComplete(await msk_get('inputAutoComplete', {input: value, maxResult: 10}))
	    } else {
	    	setAutoComplete([])
	    	history.push('/explore')
	    }
    }

    const updateField = option => {
    	setInputSearch(option)
    	setAutoComplete([])
    }

	useEffect(() => {
		async function getData(){
			setSuggestions(await msk_get('suggestionArtists', { maxResult: 11 }))			
		}
		getData()

	},[])

	function OptionsAutoComplete(){
		return(
			<OptionsCompleteSearch>
				{autoComplete.map((option, index) =>{
					return(
						<Option 
							onClick={()=> updateField(option)} 
							to={`${url}/search/${option.replaceAll(' ', '-')}`}>
							{option}
						</Option>
					)
				})}
			</OptionsCompleteSearch>
		)
	}

	return(
		<ViewPort>
			<SearchBar>
				<InputSearchBar 
					type="text" 
					name="seach" 
					value={inputSearch} 
					onInput={e => {
						setInputSearch(e.target.value)
						filterSearch(e.target.value)
					}} 
					placeholder="Artists & Songs"/>

				<BtnSearch onClick={e=> history.push(`${url}/search/${inputSearch.replaceAll(' ', '-')}`)}>
					<SearchIcon src={search_Icon} alt="search icon"/>
				</BtnSearch>

				{autoComplete && <OptionsAutoComplete/>}
				
			</SearchBar>
			<Suggestions>
				{suggestions.map((suggestion, index) => {
                    return (
                        <SuggestionBox 
                        	onClick={()=>{updateField(suggestion)}}
                        	to={`${url}/search/${suggestion.replaceAll(' ', '-')}`}
                        	key={index}>
                        	<Suggestion>{suggestion}</Suggestion>
                        </SuggestionBox>
                    )
                })}
			</Suggestions>
		</ViewPort>
	)
}

export default Search